main#container {
  position: absolute;
  display: flex;
  flex-direction: row;
  width: 80vw;
  height: 90vh;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 15px -8px black;
  border-radius: 20px;
  animation: mainCont 3s linear;
}
@keyframes mainCont {
  from {
    box-shadow: none;
  }
  to {
    box-shadow: 0 0 15px -8px black;
  }
}
@media (min-width: 320px) and (max-width: 640px) {
  main#container {
    flex-direction: column-reverse;
    width: 100vw;
    height: 100vh;
    box-shadow: none;
    border-radius: none;
  }
}
